.center-container{
    display:flex;
    justify-content: center;
    align-items: center;
    height:100vh;
  }
  
  .docked-container{
    position:fixed;
    height:100vh;
    width: 100vh;
    top:0;
    left:0;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    z-index: 1;
  }

  .auth-form-container{
    width: 35vw;
    height: 75vh;
    text-align: center;
    padding: 60px 40px;
    position:absolute;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
    border: 1px solid #000;
    backdrop-filter: blur(10px);
  }


  @media only screen and (max-width: 768px) {
    .auth-form-container{
        width: 50vw;
    }
  }
  
  @media only screen and (max-width: 460px) {
    .auth-form-container{
        width: 70vw;
    }
   
  }